<template>
  <!-- @click-overlay="closePop" -->
  <VanPopup
    v-model="popShow"
    :close-on-click-overlay="false"
    style="width: 90%; background: transparent; min-height: 500px"
    round
    get-container="content"
  >
    <img src="../assets/login-bg.png" alt="" />
    <div class="login-page">
      <template v-if="inviteShow">
        <div class="title">{{ $t('common.addInviteCode') }}</div>
        <van-field
          v-model="inviteCode"
          class="input-box"
          input-align="center"
        />
        <div class="botton-group">
          <van-button
            class="warehouse-btn"
            :disabled="!inviteCode"
            @click="handleInvteCode"
            color="#dbb037"
          >
            {{ $t('common.confirm') }}</van-button
          >
        </div>
      </template>
      <template v-else>
        <!-- 授权登录 -->
        <div class="title">{{ common.login }}</div>
        <div class="botton-group">
          <van-button
            class="warehouse-btn"
            @click="handleToLogin"
            color="#dbb037"
          >
            {{ $t('common.sign') }}
          </van-button>
        </div>
      </template>
    </div>
  </VanPopup>
</template>
<script>
import { mapActions } from 'vuex'
import { handleInviteCode, getWalletInfo } from '@/services/user'
export default {
  data () {
    return {
      popShow: true,
      message: '',
      messageHash: '', // 哈希后的数据
      signature: '', // 签名之后的字符串 长度:132,0x开头
      creatFlag: false, // 创建标识，填写邀请码
      inviteCode: '',
      inviteShow: false,
      myAccount: '',
      kolSale: false // 团队长nft预售
    }
  },
  methods: {
    ...mapActions(['changeSignData', 'changeAccount']),
    init () {
      this.getCode()
      this.getSetKolSale()
    },
    getCode () {
      this.inviteCode = this.$route.query.inviteCode || ''
    },
    getSetKolSale () {
      // this.kolSale = true
      this.kolSale = this.path === '/preSaleKol'
    },
    // 点击关闭
    closePop () {
      this.changeAccount('')
      this.$emit('hidePop') // 隐藏弹窗
    },
    // web3 登录
    async handleToLogin () {
      if (!this.myAccount) {
        const resp = await this.$web3.connectWallet()
        if (!resp.success) {
          return this.$toast(resp.message)
        }
        this.myAccount = resp.result
      }
      // 获取随机字符串
      this.message = Math.random().toString(36).slice(-8)
      // 签名
      const resp = await this.$web3.signData(this.myAccount, this.message)
      if (!resp.success) {
        this.changeAccount('')
        return this.$toast(resp.message.message)
      }
      this.signature = resp.result
      // 哈希
      const resp1 = await this.$web3.hashMessage(this.message)
      if (!resp1.success) {
        return this.$toast(resp1.message)
      }
      this.messageHash = resp1.result
      this.setData() // 存储数据
    },
    // 登录
    async setData () {
      if (!this.myAccount) return this.handleToLogin()
      const params = {
        address: this.myAccount,
        message: this.messageHash,
        signature: this.signature
      }
      this.changeSignData(params)
      this.changeAccount(this.myAccount)
      if (this.kolSale) {
        this.$emit('hidePop') // 隐藏弹窗
        this.$emit('loginSuccess')
        return
      }
      const resp = await getWalletInfo()
      if (resp.code !== 200) return
      if (resp.data.isBinding === 0) {
        this.inviteShow = true
      } else {
        this.$emit('hidePop') // 隐藏弹窗
        this.$emit('loginSuccess')
      }
    },
    async handleInvteCode () {
      const resp = await handleInviteCode({
        address: this.myAccount,
        inviteCode: this.inviteCode
      })
      if (+resp.code === 200) {
        this.$emit('hidePop') // 隐藏弹窗
        this.$emit('loginSuccess')
      }
    }
  },
  computed: {
    common () {
      return this.$t('common')
    },
    path () {
      return this.$route.path
    }
  },
  watch: {
    path (val) {
      this.getSetKolSale()
    }
  },
  mounted () {
    this.init()
  }
}
</script>
