<template>
  <div id="app">
    <!-- <BgMusic ref="bgMusic" /> -->
    <div class="router-view">
      <router-view
        @connect="
          () => {
            loginPopShow = true
          }
        "
        @changeLoading="changeLoading"
        @changeLoadingIconType="changeLoadingIconType"
        @setLoadingMessage="setLoadingMessage"
        v-if="signFlag && routerViewShow && isRouterAlive"
      ></router-view>
      <!-- 登录弹窗 -->
      <Login
        v-if="loginPopShow"
        @hidePop="hidePop"
        @loginSuccess="loginSuccess"
      />
      <!-- 加载loading -->
      <Loading v-if="isLoading">
        <div class="loading-text" v-if="loadinMessage">{{ loadinMessage }}</div>
      </Loading>
    </div>
    <div id="google_translate_element" v-if="translateFlag"></div>
    <div translate="no" class="close-btn iconfont guanbixiao" v-if="getCookie('googtrans')" @click="handleCloseTranslate"></div>
  </div>
</template>

<script>
import Loading from '@/components/common/LoadingPop.vue'
import Login from '@/components/Login.vue'
import { setTimeout } from 'timers'
import { getWalletInfo } from '@/services/user'
import { mapState, mapActions } from 'vuex'
export default {
  components: { Loading, Login },
  provide () {
    return {
      reload: this.reload
    }
  },
  data () {
    return {
      pcClass: '', // pc-style
      loginPopShow: false, // 登录
      routerViewShow: true, // 组件显示
      loadinMessage: '', // 加载提示信息
      tronWeb: null,
      walletAddress: null,
      noHeader: true,
      signFlag: false, // 签名登录标识
      isRouterAlive: true,
      googleTranslate: '',
      translateFlag: true
    }
  },
  computed: {
    ...mapState(['lang', 'myAccount', 'signData', 'isLoading'])
  },
  methods: {
    ...mapActions(['changeAccount', 'changeSignData', 'changeLoading', 'changeLoadingIconType']),
    // 刷新
    reload () {
      this.isRouterAlive = false
      this.$nextTick(() => {
        this.isRouterAlive = true
      })
    },
    // 音乐控制
    musicControl (flag) {
      if (flag) {
        this.$refs.bgMusic.$refs.musicPlay.play()
      } else {
        this.$refs.bgMusic.$refs.musicPlay.pause()
      }
    },
    init () {
      this.judgeLogin()
      this.listenWallet()
      this.getAmount()
      this.googleTranslateInit()
      this.getCookie('googtrans')
    },
    googleTranslateInit () {
      // window.location.reload()
      const lang = this.lang === 'ZH' ? 'zh-TW' : 'en'
      var script = document.createElement('script')
      script.setAttribute('data-callType', 'callScript')
      script.src = `https://translate.google.cn/translate_a/element.js?cb=googleTranslateElementInit&hl=${lang}`
      document.getElementsByTagName('body')[0].appendChild(script)
      setTimeout(() => {
        this.googleTranslateElementInit()
      }, 1500)
    },
    handleCloseTranslate () {
      this.delCookie('googtrans')
      this.translateFlag = this.getCookie('googtrans')
      // const callScript = document.querySelector("script[data-callType='callScript']")
      // document.head.removeChild(callScript)
      window.location.reload()
    },
    getCookie (name) {
      var reg = new RegExp('(^| )' + name + '=([^;]*)(;|$)')
      var arr = document.cookie.match(reg)
      console.log(arr, 'getCookie')
      return arr
    },
    delCookie (name) {
      var Days = 30
      var exp = new Date()
      exp.setTime(exp.getTime() - Days * 24 * 60 * 60 * 30)
      // 这里一定要注意，如果直接访问ip的话，不用注明域名domain
      // 但访问的是域名例如www.baidu.com时，翻译插件的cookie同时存在于一级和二级域名中
      // 即删除翻译cookie时要把domain=www.baidu.com和domain=.baidu.com两个cookie一起删除才行
      var domain = document.domain
      var domainIsIp = false
      var dd = domain.split('.')
      if (dd.length === 4) {
        domainIsIp = true
      }
      document.cookie = name + "='';path=/;expires=" + exp.toUTCString()
      if (domainIsIp === false) {
        domain = '.' + dd[1] + '.' + dd[2]
        document.cookie = name + "='';domain=" + domain + ';expires=' + exp.toGMTString() + ';path=/'
      }
    },
    googleTranslateElementInit () {
      const google = window.google
      console.log(window.google)
      setTimeout(() => {
        this.googleTranslate = new google.translate.TranslateElement(
          {
            // 这个参数不起作用，看文章底部更新，翻译面板的语言
            // pageLanguage: 'en',
            // 这个是你需要翻译的语言，比如你只需要翻译成越南和英语，这里就只写en,vi
            // includedLanguages: 'en,zh-TW,zh-CN,hr,cs,da,nl,fr,de,el,iw,hu,ga,it,ja,ko,pt,ro,ru,sr,es,th,vi',
            includedLanguages: 'en,ko,zh-TW,vi,ja,th,id',
            // 选择语言的样式，这个是面板，还有下拉框的样式，具体的记不到了，找不到api~~
            // layout: 'google.translate.TranslateElement.InlineLayout.VERTICAL',
            // /mobile/i.test(navigator.userAgent) ? 0 : 2,
            layout: 0,
            // layout: google.translate.TranslateElement.InlineLayout.SIMPLE,
            // 自动显示翻译横幅，就是翻译后顶部出现的那个，有点丑，这个属性没有用的话，请看文章底部的其他方法
            autoDisplay: false
            // 还有些其他参数，由于原插件不再维护，找不到详细api了，将就了，实在不行直接上dom操作
          },
          'google_translate_element'// 触发按钮的id
        )
        // 清除图片的请求
        var img = [].slice.call(document.querySelectorAll('#goog-gt-tt img,#google_translate_element img'))
        img.forEach(function (v, i) {
          v.src = ''
        })
      }, 1500)
    },
    // 获取判断用户登录
    async getAmount () {
      const resp = await this.$web3.connectWallet()
      if (!resp.success) return
      const account = resp.result
      this.changeAccount(account)
      if (this.signData && this.signData.address === account) {
        const resp = await getWalletInfo()
        if (resp.data.isBinding === 0) {
          this.signFlag = false
          this.loginPopShow = true
          return
        }
        this.$nextTick(() => {
          this.signFlag = true
          this.routerViewShow = true
        })
      } else {
        this.$nextTick(() => {
          // console.log('false', 'show')
          this.loginPopShow = true
          this.signFlag = false
          this.routerViewShow = false
        })
      }
    },
    // 钱包监听
    listenWallet () {
      // 账号切换
      this.$web3.ethereum.on('accountsChanged', (accounts) => {
        console.log('accountsChanged')
        if (this.$route.path !== '/preSaleKol') {
          this.$gbUtils.handleToPage('/home')
        }
        this.signFlag = false
        this.routerViewShow = false
        this.changeAccount('')
        this.changeSignData({
          address: '',
          signature: '',
          message: ''
        })
        this.getAmount()
        // this.judgeLogin()
      })
      // 断开链接
      this.$web3.ethereum.on('disconnect', (res) => {
        console.log('disconnect')
        this.signFlag = false
        this.routerViewShow = false
        this.changeAccount('')
        this.changeSignData({
          address: '',
          signature: '',
          message: ''
        })
        this.getAmount()
        // this.judgeLogin()
      })
    },
    // 判断当前终端
    isMobile () {
      const flag = navigator.userAgent.match(
        /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
      )
      return flag
    },
    // 隐藏登录弹窗
    hidePop () {
      this.loginPopShow = false
    },
    // 登录成功
    loginSuccess () {
      this.routerViewShow = false
      this.getAmount()
      this.loginPopShow = false
      this.$nextTick(() => {
        this.routerViewShow = true
      })
    },
    // 判断是否登录
    judgeLogin () {
      if (!this.signData.signature) {
        console.log('no account')
        console.log('lthis.oginPopShow', this.loginPopShow)
        if (this.loginPopShow) return
        this.loginPopShow = true
      }
    },
    // 获取加载信息
    setLoadingMessage (message) {
      this.loadinMessage = message
    }
  },
  mounted () {
    this.init()
  },
  watch: {
    $route (to, from) {
      this.noHeader = to.meta.noHeader || false
    },
    signData (val) {
      this.getAmount()
    },
    lang (val) {
      this.delCookie('googtrans')
      window.location.reload()
    }
  }
}
</script>

