import axios from 'axios'
import store from '@/store'
import config from '@/config/app.config'
import { Toast } from 'vant'

axios.defaults.crossDomain = true
axios.defaults.timeout = 300000

const request = (
  method,
  url,
  params,
  needToken = true,
  loading,
  loadingIconType,
  headerContentType = 'application/json',
  showErrorMessage = true
) => {
  const headers = {
    'Content-Type': headerContentType
  }

  if (needToken) {
    const signData = store.state.signData
    if (!signData || !signData.address) {
      store.commit('SET_MY_ACCOUNT', '')
      store.commit('SET_SIGN', '')
      return
    } else if (store.state.myAccount !== signData.address) {
      store.commit('SET_MY_ACCOUNT', '')
      store.commit('SET_SIGN', '')
      return
    }
    headers.message = signData.message
    headers.signature = signData.signature
    headers.address = signData.address
  }
  if (loading) {
    // 显示全局加载loading
    store.state.isLoading = true
  } else {
  }
  if (loadingIconType) {
    store.state.loadingIconType = loadingIconType
  } else if (loading) {
    store.state.loadingIconType = ''
  }
  return new Promise((resolve, reject) => {
    axios({
      method,
      headers,
      baseURL: config.server,
      url,
      timeout: 15000,
      params: method === 'GET' || method === 'DELETE' ? params : null, // 是即将与请求一起发送的 URL 参数
      data: method === 'POST' || method === 'PUT' ? params : null // 是作为请求主体被发送的数据
    })
      .then((res) => {
        // 全局加载样式隐藏
        setTimeout(() => {
          store.state.isLoading = false
        }, 500)
        Toast.clear()
        if (res.data.code === 422) {
          store.commit('SET_MY_ACCOUNT', '')
          resolve({
            success: false,
            records: [],
            result: {},
            total: 0,
            message: '登录已失效，请重新登录'
          })
        } else if (res.data.code === 200) {
          res.data.success = true
          resolve(res.data)
        } else {
          res.data.success = false
          const messageText = res.data.message || res.data.msg
          // 如果返回的msg是”not throw“,则不显示
          if (messageText !== 'not throw') {
            Toast.fail(messageText, 5)
          }
          resolve(res.data)
        }
      })
      .catch((error) => {
        Toast.clear()
        store.state.isLoading = false
        let messageText = ''
        if (
          error.response &&
          error.response.data &&
          (error.response.data.message || error.response.data.msg)
        ) {
          messageText = error.response.data.message || error.response.data.msg
        } else {
          // messageText = error.response ? getErrorMessage(error.response.status) : 'Network exception'
          messageText = 'Network exception'
        }
        // 如果返回的msg是”not throw“,则不显示
        if (messageText !== 'not throw') {
          Toast.fail(messageText, 5)
        }
        // reject(error)
        resolve({
          success: false,
          records: [],
          result: {},
          total: 0,
          message: 'Network exception'
        })
      })
  })
}

export default request
