import {
  setUrlPath,
  setNftPath,
  getRoleImg,
  getSatiety,
  getExploreSatiety,
  getFullSatiety,
  getNftAddress
} from './index'
import router from '../router'

const handleToPage = (url) => {
  if (url) {
    // 跳转
    router.push(url)
  } else {
    router.replace('/')
  }
}

// 钱包地址格式化
const formatAddress = (value, flag = '.', num = 3) => {
  if (!value) return 0
  const startAddress = value.slice(0, num)
  const endAddress = value.slice(-num)
  const address = startAddress + flag + endAddress
  return address
}

// 货币四位小数
const formatBalance = (value) => {
  value = Number(value.toString().match(/^\d+(?:\.\d{0,4})?/))
  return value
}

// 货币两位小数
const formatTwoBalance = (value) => {
  value = Number(value.toString().match(/^\d+(?:\.\d{0,2})?/))
  return value
}

// 都转为小写
const toUpperCase = (value) => {
  return value.toUpperCase()
}

// 都转为小写
const toLowerCase = (value) => {
  return value.toLowerCase()
}

// 数字字符左侧补零---总5位
const setNftNum = (num) => {
  num = num || 0
  return num.toString().padStart(5, '0')
}

const handleRound = (num, iCount = 4) => {
  // iCount 保留几位小数
  let changeNum = num
  let zs = true
  // 判断是否是负数
  if (changeNum < 0) {
    changeNum = Math.abs(changeNum)
    zs = false
  }
  const iB = Math.pow(10, iCount)
  // 有时乘100结果也不精确
  const value1 = changeNum * iB
  let intDecSet = []
  let intDecHun = []

  let fValue = value1
  const value2 = value1.toString()
  const iDot = value2.indexOf('.')

  // 如果是小数
  if (iDot !== -1) {
    intDecSet = changeNum.toString().split('.')

    // 如果是科学计数法结果
    if (intDecSet[1].indexOf('e') !== -1) {
      return Math.round(value1) / iB
    }
    intDecHun = value2.split('.')

    if (intDecSet[1].length <= iCount) {
      return parseFloat(num, 10)
    }

    const fValue3 = parseInt(intDecSet[1].substring(iCount, iCount + 1), 10)

    if (fValue3 >= 5) {
      fValue = parseInt(intDecHun[0], 10) + 1
    } else {
      // 对于传入的形如111.834999999998 的处理（传入的计算结果就是错误的，应为111.835）
      if (
        fValue3 === 4 &&
        intDecSet[1].length > 10 &&
        parseInt(intDecSet[1].substring(iCount + 1, iCount + 2), 10) === 9
      ) {
        fValue = parseInt(intDecHun[0], 10) + 1
      } else {
        fValue = parseInt(intDecHun[0], 10)
      }
    }
  }
  // 如果是负数就用0减四舍五入的绝对值
  let val = zs ? fValue / iB : 0 - fValue / iB

  const d = val.toString().split('.')
  if (d.length === 1) {
    return val.toString() + '.00'
  }
  if (d.length > 1) {
    if (d[1].length < 2) {
      val = val.toString() + '0'
    }
    return val
  }
}

function timeStamp (StatusMinute) {
  var day = parseInt(StatusMinute / 60 / 24)
  var hour = parseInt((StatusMinute / 60) % 24)
  var min = parseInt(StatusMinute % 60)
  StatusMinute = ''
  if (day > 0) {
    StatusMinute = day + '天'
  }
  if (hour > 0) {
    StatusMinute += hour + '小时'
  }
  if (min >= 0) {
    StatusMinute += parseFloat(min) + '分钟'
  }
  return StatusMinute
}

function shuffle (arr) {
  var l = arr.length
  var index, temp
  while (l > 0) {
    index = Math.floor(Math.random() * l)
    temp = arr[l - 1]
    arr[l - 1] = arr[index]
    arr[index] = temp
    l--
  }
  return arr
}
const gbUtils = {
  setUrlPath,
  setNftPath,
  setNftNum,
  getRoleImg,
  getSatiety,
  getExploreSatiety,
  getFullSatiety,
  getNftAddress,
  handleToPage,
  formatBalance,
  formatTwoBalance,
  formatAddress,
  toUpperCase,
  toLowerCase,
  handleRound,
  timeStamp,
  shuffle
}

export default gbUtils
