import i18n from '../main.js'
export default {
  SET_MY_INFO (state, myInfo) {
    state.myInfo = myInfo
    localStorage.setItem('myInfo', JSON.stringify(myInfo))
  },
  SET_MY_ACCOUNT (state, myAccount) {
    state.myAccount = myAccount
  },
  SET_LOADING (state, isLoading) {
    state.isLoading = isLoading
  },
  SET_LOADING_ICON_TYPE (state, loadingIconType) {
    state.loadingIconType = loadingIconType
  },
  SET_SIGN (state, signData) {
    if (signData) {
      localStorage.setItem('sign', JSON.stringify(signData))
    } else {
      localStorage.removeItem('sign')
    }
    state.signData = signData
  },
  SET_LANG (state, lang) {
    state.lang = lang
    if (i18n) {
      i18n.locale = lang // i18n语言切换
    }
    if (lang) {
      localStorage.setItem('lang', lang)
    } else {
      localStorage.removeItem('lang')
    }
  }
}
