import devpConfig from './devp.config'
import prodConfig from './prod.config'
import testConfig from './test.config'
import testAddressConfig from './testAddress'
import prodAddressConfig from './prodAddress'
import preAddressConfig from './preAddress'

let config
let addressConfig

switch (process.env.RELEASE_ENV) {
  case 'prod':
    config = prodConfig
    addressConfig = prodAddressConfig
    break
  case 'test':
    console.log(process.env.RELEASE_ENV)
    config = testConfig
    addressConfig = preAddressConfig
    // addressConfig = testAddressConfig
    break
  default:
    console.log('process.env.RELEASE_ENV')
    config = devpConfig
    // config = testConfig
    addressConfig = testAddressConfig
    // console.log(addressConfig)
    addressConfig = preAddressConfig
  // config = prodConfig
  // 开启跨域，调试正式服的时候需要打开
  // config.server = '/api'
}

const projectName = 'dapp'

export default {
  ...config,
  ...addressConfig,
  projectName
}
