import config from '@/config/app.config'

// 给文件添加域名
export const setUrlPath = (url) => {
  if (!url) {
    return ''
  }
  const index = url.indexOf('http')
  const firstChar = url.charAt(0)
  if (index === 0) return url
  if (firstChar === '/') {
    return `${config.imgUrl}${url}`
  }
  if (firstChar !== '/') {
    return `${config.imgUrl}/${url}`
  }
}
// setNftPath
export const setNftPath = (type, num) => {
  console.log('num', num)
  console.log('type', type)
  type = parseInt(type)
  var path
  switch (type) {
    case 0:
      path = 'usual/'
      break
    case 2:
      path = 'organization/'
      break
    case 3:
      path = 'media/'
      break
    default:
      path = 'kol/'
      break
  }
  const count = (type === 2 || type === 3) ? 4 : 5
  const fullNum = num.toString().padStart(count, '0')
  return `${config.imgUrl}${path}${fullNum}.png`
}

export const getRoleImg = (type) => {
  // 类型 0普通 1团长 2机构 3媒体
  type = parseInt(type)
  switch (type) {
    case 1:
      return require('../assets/star/main/kol.png')
    case 2:
      return require('../assets/star/main/organ.png')
    case 3:
      return require('../assets/star/main/media.png')
  }
}

// 获取卡片消耗饱食du
export const getSatiety = (myInfo) => {
  if (myInfo.type === 1) return 30.864
  switch (myInfo.level) {
    case 1:
      return 1.662
    case 2:
      return 2.345
    case 3:
      return 3.84
    case 4:
      return 7.2
    case 5:
      return 15.55
    default:
      return 1.662
  }
}

