// 获取卡片饱食度上限
export const getFullSatiety = (myInfo) => {
  if (myInfo.type === 1) return 800
  switch (myInfo.level) {
    case 1:
      return 100
    case 2:
      return 150
    case 3:
      return 200
    case 4:
      return 300
    case 5:
      return 500
    default:
      return 100
  }
}

// 获取卡片合约
export const getNftAddress = (myInfo) => {
  // 类型 0普通 1团长 2机构 3媒体
  switch (myInfo.type) {
    case 1:
      return config.KOL
    case 2:
      return config.SPT
    case 3:
      return config.SMT
    default:
      return config.SUT
  }
}
