import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

let myAccount, isLoading, loadingIconType, lang

var myInfo = {
  level: 0,
  fragments: 0,
  nickName: '',
  experience: 0,
  isTravel: false,
  satiety: 0
}

var signData = {
  message: '',
  signature: '',
  address: ''
}

console.log(myAccount)

try {
  isLoading = false
} catch {
  isLoading = false
}

const tempSignData = localStorage.getItem('sign')
if (tempSignData) {
  signData = JSON.parse(tempSignData)
}
try {
  myInfo = JSON.parse(localStorage.getItem('myInfo'))
} catch {
  myInfo = null
}
try {
  lang = localStorage.getItem('lang')
} catch {
  localStorage.setItem('lang', 'EN')
  lang = 'EN'
}

export default {
  myAccount, // 钱包账户地址
  isLoading,
  loadingIconType, // loading的icon
  signData,
  lang,
  myInfo
}
