import request from '@/utils/request'

// 填写邀请码
export const handleInviteCode = (params) => {
  return request('POST', '/dapp/wallet/bidding', params, true, true, 0)
}

// 用户首页信息
export const getWalletInfo = (params) => {
  return request('GET', '/dapp/wallet/walletInfo', params)
}

// 修改昵称
export const handleNickname = (params) => {
  return request('POST', '/dapp/wallet/editWallet', params, true, true, 0)
}

// 升级页面
export const handleUpgradePage = (params) => {
  return request('GET', '/dapp/wallet/upgrade', params, true, true, 1)
}

// 升级
export const handleUpgrade = (params) => {
  return request('POST', '/dapp/wallet/upgrade', params, true, true, 0)
}
